
.mi-chip {
  opacity: 0.7;
  padding: 0 9px;
  -webkit-transition: 0.15s ease-in-out;
  transition: 0.15s ease-in-out;
  font-family: 'Hind', sans-serif;
  border-radius: 12px;
  border: 1px solid;
  z-index: 1;
  cursor: pointer;
}
.mi-chip:hover {
  opacity: 1
}



