
.projects {
  margin-top: 10vh;
  overflow: hidden;
}
.intro {
  margin-left: 22vw;
  margin-right: 22vw;
}
.project {
  margin-bottom: 2.5vh;
}
.project-view {
  z-index: 3;
  position: fixed;
  top: 0;
  width: 100vw;
  height: 100vh;
}
.fade-enter-active {
  -webkit-transition: all .2s ease-out;
  transition: all .2s ease-out;
}
.fade-enter, .fade-leave-to {
  opacity: 0;
}



